        <div class="mb-1">
          <img class="role-icon mr-2" :src="'/icons/' + summoner.role + '.png'">
          {{summoner.name}}
        </div>

        <div class="text-center win-loss">
          <span class="text-success">4</span> <span class="text-white-50">|</span> <span class="text-danger">17</span>
        </div>
        <div class="text-center small text-white text-muted">21 Clash Games</div>


      </th>
    </tr>
    </thead>
    <tbody>

    <th></th>
    <th></th>
    <th class="p-1 font-weight-normal text-center">Clash</th>
    <th></th>
    <th></th>

    <tr v-for="row in matchHistory.clashRows" v-bind:key="'clash_rows_' + row._id">
