<template>
  <td :class="matchBackground(match.won)">
    <div class="row">
      <div class="col-12 text-white-50">
        <span class="badge badge-secondary mb-1">{{ match.gameMode }}</span> <span v-if="!match.won" class="badge badge-danger">Lost</span>
        <span v-if="match.won" class="badge badge-success">Won</span> <span
          class="small"> in {{ match.duration }}</span>

        <span class="small ml-1">
          <span class="badge badge ">Level {{ match.level }}</span>
        </span>
      </div>


      <div class="col-4 mt-2 pr-1">
        <img
            :src="match.champion"
            alt="Ahri"
            class="rounded img-fluid text-center champion-icon"

        ><br>

      </div>
      <div class="col-8 pl-0">

        <div class="row">
          <div class="col-6 pr-0">
            <div class="badge">
              <img class="role-icon pr-1" :src="'/icons/' + match.role + '.png'">
              <span class="text-capitalize">{{ match.role }}</span>
            </div>

          </div>
          <div class="col-6 pl-0">
            <div class="badge">
              <i class="fas fa-chart-line pr-1"></i>
              <span class="text-white">{{ match.cs }} ({{ match.csMinute }})</span>
            </div>
          </div>
          <div class="col-6 pr-0">
            <div class="badge">
              <span class="text-white">{{ match.k }}/{{ match.d }}/{{ match.a }}</span>
            </div>
          </div>
          <div class="col-6 pl-0">
            <div class="badge">
              <i class="fas fa-coins pr-1"></i>
              <span class="text-white">{{ match.goldShare }} </span>
            </div>
          </div>
        </div>




      </div>
    </div>
  </td>
</template>

<script>
export default {
  name: "MatchHistoryMatchBlock",
  props: {
    match: Object
  },
  methods: {
    matchBackground(win) {
      if (win === true) return 'table-bg-won'
      else return 'table-bg-lost'
    }
  }
}
</script>

<style scoped>
.table-bg-lost {
  background: #672d36;
}

.table-bg-won {
  background: #2a7658;
}

.role-icon {
  max-height: 16px;
  width: auto;
}

.champion-icon {
  max-height: 45px;
}
</style>